.main-nav {
  margin-right: 144px;
  align-self: center;

  @include vp-1439 {
    margin: 0 auto;
  }

  @include vp-1023 {
    margin-left: auto;
    margin-right: 20px;
  }

  @include vp-767 {
    margin-right: 10px;
    margin-bottom: 0;
  }

  &__list {
    max-width: 550px;
    min-width: 480px;
    margin: 0;
    padding: 0;

    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 10px 20px;
    list-style: none;

    @include vp-1023 {
      display:  flex;
      flex-wrap: wrap;
      min-width: auto;
      max-width: 420px;

      gap: 0;
    }

    @include vp-767 {
      justify-content: space-between;

      min-width: 100px;
      max-width: 200px;
    }
  }

  &__item {
    display: flex;

    @include vp-1023{
      margin-right: 20px;
      margin-bottom: 10px;

      &:last-child {
      margin-right: 0;
    }
    }
  }

  &__icon {
    margin-right: 15px;

    @include vp-1023 {
      margin: 0;
    }
  }

  &__link {
    display: flex;
    color: inherit;
    font-family: inherit;
    cursor: pointer;

    transition: $trans-default;

    &:hover {
      scale: 1.1;
    }

    &:active {
      color: $color-deep-sky-blue;
    }

    @include vp-1023 {
      min-width: 40px;
      min-height: 40px;
      align-items: center;
      justify-content: center;
    }
  }

  &__link-text {
    @include vp-1023 {
      display: none;
    }
  }
}
